import {
  createUserWithEmailAndPassword,
  FacebookAuthProvider,
  GoogleAuthProvider,
  signInWithEmailAndPassword,
  signInWithPopup,
} from "firebase/auth";
import React, { useState, useEffect } from "react";
import { authService } from "../fbase";
import { useTitle } from "../hooks/useTitle";
import { useNavigate } from "react-router-dom";

const Auth = (): JSX.Element => {
  const [form, setForm] = useState({ email: "", password: "" });
  const [newAccount, setNewAccount] = useState(false);
  const [errorCode, setErrorCode] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const onChange = ({
    target: { name, value },
  }: {
    target: { name: string; value: string };
  }) => setForm({ ...form, [name]: value });
  const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    let data;
    if (form.email === "") {
      setErrorMessage(" .");
    } else if (!/\S+@\S+\.\S+/.test(form.email)) {
      setErrorMessage("  .");
    } else if (form.password === "") {
      setErrorMessage(" .");
    }

    if (newAccount) {
      // 
      data = await createUserWithEmailAndPassword(
        authService,
        form.email,
        form.password
      ).catch((error) => {
        console.log(error.code);
        setErrorCode(error.code);
      });
      navigate("/setusername");
    } else {
      // 
      data = await signInWithEmailAndPassword(
        authService,
        form.email,
        form.password
      ).catch((error) => {
        console.log(error.code);
        setErrorCode(error.code);
      });
    }
  };
  const toggleAccount = () => {
    setNewAccount((prev) => !prev);
  };

  const onSocialClick = async (e: React.MouseEvent<HTMLButtonElement>) => {
    let provider;
    if ((e.target as HTMLButtonElement).name === "google") {
      provider = new GoogleAuthProvider();
    } else if ((e.target as HTMLButtonElement).name === "facebook") {
      provider = new FacebookAuthProvider();
    } else if ((e.target as HTMLButtonElement).name === "naver") {
      console.log("");
      //  
    }
    const data = await signInWithPopup(
      authService,
      provider as GoogleAuthProvider | FacebookAuthProvider
    ).catch((error) => {
      console.log(error);
    });
    console.log(data);
  };

  useEffect(() => {
    if (errorCode === "auth/email-already-in-use") {
      setErrorMessage("   .");
    } else if (errorCode === "auth/weak-password") {
      setErrorMessage(" 6  .");
    } else if (errorCode === "auth/user-not-found") {
      setErrorMessage("   .");
    } else if (
      errorCode === "auth/wrong-password" &&
      form.password.length < 6
    ) {
      setErrorMessage(" 6  .");
    } else if (errorCode === "auth/wrong-password") {
      setErrorMessage("   .");
    }
  }, [errorCode]);

  useTitle("J-Diary - ");

  return (
    <div>
      <form onSubmit={onSubmit}>
        <input
          name="email"
          type="text"
          placeholder="Email"
          value={form.email}
          onChange={onChange}
        />
        <input
          name="password"
          type="password"
          placeholder="Password"
          value={form.password}
          onChange={onChange}
        />
        <input type="submit" value={newAccount ? "" : ""} />
        <span onClick={toggleAccount}>
          {newAccount ? "  ?" : "  ?"}
        </span>
      </form>
      <span>{errorMessage}</span>
      <div>
        <button name="google" onClick={onSocialClick}>
           
        </button>
        <button name="facebook" onClick={onSocialClick}>
           
        </button>
      </div>
    </div>
  );
};

export default Auth;
